import { useEffect, useState } from "react";
import InputWrapper from "./Counter.styles";

const Counter = ({ count, disabled = false, inputValue, onInput }) => {
  const [inputWidth, setInputWidth] = useState(0);
  const numRegex = /^[0-9]+$/g;

  const checkInput = (value) => {
    let checkedValue = value;

    if (!numRegex.test(checkedValue)) {
      value === "" ? (checkedValue = 0) : (checkedValue = inputValue);
    }
    return checkedValue;
  };

  const onInputNumber = (e) => {
    let { value } = e.target;
    const filteredValue = checkInput(value);
    setInputWidth(filteredValue.length * 10 || 10);
    onInput && onInput(filteredValue);
  };

  useEffect(() => {
    setInputWidth(count.toString().length * 10 || 10);
    onInput && onInput("");
  }, [count]);

  return (
    <InputWrapper>
      <input
        type="text"
        placeholder={count}
        disabled={disabled}
        onInput={(e) => onInputNumber(e)}
        maxLength={3}
        value={inputValue || ""}
        style={{ width: inputWidth }}
      />
      명
    </InputWrapper>
  );
};

export default Counter;
