import ListContainer from "./DropDown.styles";

const DropDown = ({ items, onClick }) => {
  return (
    <ListContainer>
      <ul>
        {items.map((item, idx) => {
          return (
            <li key={idx} onClick={() => onClick(item)}>
              {item.team || item.dept || `${item.name} / ${item.belong}`}
            </li>
          );
        })}
      </ul>
    </ListContainer>
  );
};

export default DropDown;
