import { useCallback, useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { Button, LotteryContainer } from "./LotteryPage.styles";
import { ReactComponent as Back } from "static/images/svgs/back.svg";
import { ReactComponent as Replay } from "static/images/svgs/replay.svg";
import Lottery from "components/lottery/Lottery";

const LotteryPage = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const [applicants, setApplicants] = useState([]);
  const [replay, setReplay] = useState(0);

  const { selectedItems, countInput: count } = location.state;

  const searchAndAddPeople = useCallback((data) => {
    let results = [];

    if (typeof data === "object") {
      Object.keys(data).forEach((key) => {
        if (
          typeof data[key] === "string" &&
          key === "name" &&
          data[key] !== ""
        ) {
          results.push(data);
        } else {
          const nestedResults = searchAndAddPeople(data[key]);
          results = results.concat(nestedResults);
        }
      });
    }

    return results;
  }, []);

  const onClickBack = () => {
    navigate(-1);
  };

  const onClickReplay = () => {
    setReplay(replay + 1);
  };

  useEffect(() => {
    setApplicants(() => searchAndAddPeople(selectedItems));
  }, []);

  return (
    <LotteryContainer>
      <Lottery
        applicants={applicants.length ? applicants : count}
        replay={replay}
      />
      <Button className="back-btn" onClick={onClickBack}>
        <Back width={22} />
      </Button>
      <Button className="replay-btn" onClick={onClickReplay}>
        <Replay width={24} />
      </Button>
    </LotteryContainer>
  );
};

export default LotteryPage;
