export default class Mover {
  constructor({
    img,
    color,
    burstImg,
    name,
    team,
    startX,
    startY,
    font,
    size,
    p5,
  }) {
    this.p5 = p5;
    this.font = font;
    // IMAGE 초기 세팅
    this.img = img;
    this.sizeW = Math.round(this.minMax(size, 30, 150));
    this.sizeH = (img.height * this.sizeW) / img.width;
    this.r = this.sizeW / 2;
    this.fontSize = 0;

    this.color = color;
    this.burstImg = burstImg;

    this.name = name;
    this.team = team;
    this.pos = this.p5.createVector(startX, startY);

    this.vel = this.p5.createVector(0, 0);

    this.center = p5.createVector(p5.width / 2, p5.height / 2);
    this.acc = p5.constructor.Vector.sub(this.center, this.pos);
    this.acc = this.acc.normalize();
    this.acc = this.acc.mult(0.25);

    this.isStart = true;
    this.colCount = 0;
    this.isDone = false;

    this.hasScale = false;
    this.scale = 0;

    this.alpha = 255;
  }

  minMax(num, min, max) {
    let newNum = num;
    if (num > max) {
      newNum = max;
    }
    if (num < min) {
      newNum = min;
    }
    return newNum;
  }

  changeDirection(pos) {
    this.vel[pos] *= -1;
    this.acc.mult(0);
  }

  stop() {
    this.acc = this.acc.mult(0);
    this.vel = this.vel.mult(0);
  }

  isInsideScreen() {
    return (
      this.pos.x > this.r &&
      this.pos.x < this.p5.width - this.r &&
      this.pos.y > this.r &&
      this.pos.y < this.p5.height - this.r
    );
  }

  checkEdges() {
    if (this.isInsideScreen()) {
      this.isStart = false;
    }
    if (this.isStart) return;

    switch (true) {
      case this.pos.x > this.p5.width - this.r && this.vel.x > 0:
        this.changeDirection("x");
        this.addColCount();
        break;
      case this.pos.x < this.r && this.vel.x < 0:
        this.changeDirection("x");
        this.addColCount();
        break;
      case this.pos.y + this.r > this.p5.height && this.vel.y > 0:
        this.changeDirection("y");
        this.addColCount();
        break;
      case this.pos.y - this.r < 0 && this.vel.y < 0:
        this.changeDirection("y");
        this.addColCount();
        break;
      default:
        break;
    }
  }

  checkCollision(other) {
    if (this.isInsideScreen()) {
      this.isStart = false;
    }
    if (this.isDone) {
      return;
    }
    if (!other) return;
    if (other.isDone || this.isStart) return;

    const { Vector } = this.p5.constructor;
    // Get distances between the balls components
    let distanceVect = Vector.sub(other.pos, this.pos);

    // Calculate magnitude of the vector separating the balls
    let distanceVectMag = distanceVect.mag();

    // Minimum distance before they are touching
    let minDistance = this.r + other.r;

    if (distanceVectMag < minDistance - 5) {
      return;
    }
    if (distanceVectMag < minDistance) {
      this.addColCount();

      let distanceCorrection = (minDistance - distanceVectMag) / 2.0;
      let d = distanceVect.copy();
      let correctionVector = d.normalize().mult(distanceCorrection);
      other.pos.add(correctionVector);
      this.pos.sub(correctionVector);

      // get angle of distanceVect
      let theta = distanceVect.heading();
      // precalculate trig values
      let sine = this.p5.sin(theta);
      let cosine = this.p5.cos(theta);

      let bTemp = [new Vector(), new Vector()];

      bTemp[1].x = cosine * distanceVect.x + sine * distanceVect.y;
      bTemp[1].y = cosine * distanceVect.y - sine * distanceVect.x;

      // rotate Temporary velocities
      let vTemp = [new Vector(), new Vector()];

      vTemp[0].x = cosine * this.vel.x + sine * this.vel.y;
      vTemp[0].y = cosine * this.vel.y - sine * this.vel.x;
      vTemp[1].x = cosine * other.vel.x + sine * other.vel.y;
      vTemp[1].y = cosine * other.vel.y - sine * other.vel.x;

      let vFinal = [new Vector(), new Vector()];

      // final rotated this.vel for b[0]
      vFinal[0].x = (2 * vTemp[1].x) / 2;
      vFinal[0].y = vTemp[0].y;

      // final rotated this.vel for b[0]
      vFinal[1].x = (2 * vTemp[0].x) / 2;
      vFinal[1].y = vTemp[1].y;

      // hack to avoid clumping
      bTemp[0].x += vFinal[0].x;
      bTemp[1].x += vFinal[1].x;

      /* Rotate ball this.poss and velocities back
       Reverse signs in trig expressions to rotate 
       in the opposite direction */
      // rotate balls
      let bFinal = [new Vector(), new Vector()];

      bFinal[0].x = cosine * bTemp[0].x - sine * bTemp[0].y;
      bFinal[0].y = cosine * bTemp[0].y + sine * bTemp[0].x;
      bFinal[1].x = cosine * bTemp[1].x - sine * bTemp[1].y;
      bFinal[1].y = cosine * bTemp[1].y + sine * bTemp[1].x;

      // update balls to screen this.pos
      other.pos.x = this.pos.x + bFinal[1].x;
      other.pos.y = this.pos.y + bFinal[1].y;

      this.pos.add(bFinal[0]);

      // update velocities
      this.vel.x = cosine * vFinal[0].x - sine * vFinal[0].y;
      this.vel.y = cosine * vFinal[0].y + sine * vFinal[0].x;
      other.vel.x = cosine * vFinal[1].x - sine * vFinal[1].y;
      other.vel.y = cosine * vFinal[1].y + sine * vFinal[1].x;
    }
  }

  addColCount() {
    if (!this.isDone) {
      this.colCount += 1;
    }
  }

  setScaleToCenter() {
    const resultSize = this.p5.width;
    const distToCenter = Math.round(this.center.dist(this.pos));
    const diffSize = resultSize - this.sizeW;
    const scale = Math.round(diffSize / distToCenter);

    this.scale = scale;
  }

  setWinnerMove() {
    if (this.isDone) {
      return;
    }
    if (!this.hasScale) {
      this.hasScale = true;
      this.setScaleToCenter();
    }

    this.vel.mult(0);
    this.acc = this.p5.constructor.Vector.sub(this.center, this.pos);
    this.acc = this.acc.normalize();
    this.acc.mult(3);
    this.pos.add(this.acc);

    this.sizeW += this.scale;
    this.sizeH += this.scale;
    this.fontSize = Math.round(this.sizeW / 5);

    if (this.center.dist(this.pos) < 5) {
      this.p5.noLoop();
    }
  }

  setFailureMove() {
    this.vel.mult(0);
    this.acc.y += 1;
  }

  setOpacity(alpha) {
    this.alpha = alpha;
  }

  update() {
    this.vel.add(this.acc);
    this.pos.add(this.vel);
  }

  burst() {
    this.p5.tint(255, 255);
    this.burstImg.size(this.size, this.size);
    this.burstImg.position(this.pos.x, this.pos.y);
  }

  display() {
    this.p5.tint(255, this.alpha);
    this.p5.image(this.img, this.pos.x, this.pos.y, this.sizeW, this.sizeH);

    this.p5.textAlign(this.p5.CENTER);
    this.p5.textFont(this.font, this.fontSize);
    this.p5.fill(0, 0, 0);
    this.p5.text(this.name, this.pos.x, this.pos.y + (this.sizeH / 9) * 7);

    this.p5.textSize((this.fontSize / 3) * 2);
    this.p5.fill(150, 150, 150);
    this.p5.text(
      this.team,
      this.pos.x,
      this.pos.y + (this.sizeH / 9) * 7 + this.fontSize
    );
  }
}
