import { useEffect, useState } from "react";
import { ReactP5Wrapper } from "@p5-wrapper/react";
import Mover from "./Mover";
import Burster from "./Burster";

const Lottery = ({ applicants, replay }) => {
  let shapes = [
    { shape: "circle", color: "yellow" },
    { shape: "rectangle", color: "blue" },
    { shape: "triangle", color: "green" },
    { shape: "star", color: "yellow" },
    { shape: "flower", color: "pink" },
    { shape: "halfmoon", color: "green" },
    { shape: "hexagon", color: "pink" },
    { shape: "plus", color: "blue" },
  ];
  let characters = [];
  let movers = [];
  let leftMovers = [];
  let bursters = [];
  let fontStyle;

  const [nameArr, setNameArr] = useState([]);

  const setNamesInArray = (applicants) => {
    if (typeof applicants === "number") {
      return Array.from({ length: applicants }, (_, i) => {
        return { name: i + 1, team: "" };
      });
    }
    return applicants.map((applicant) => {
      return { name: applicant.name.split(" ")[0], team: applicant.belong };
    });
  };

  const setCharactersWithNames = () => {
    let newCharacters = [];
    newCharacters = nameArr.map((info, idx) => {
      idx = idx % shapes.length;
      return {
        name: info.name,
        team: info.team,
        img: characters[idx].img,
        color: characters[idx].color,
      };
    });
    return newCharacters;
  };

  const setSizeCharacter = (p5) => {
    const screenSize = p5.width * p5.height;
    const size = Math.sqrt(screenSize / characters.length) / 2;
    return size;
  };

  const setStartPosition = (index, p5) => {
    const posIdx = index % 4;
    let position = { x: 0, y: 0 };

    const { width, height } = p5;
    const randomWidthValue = Math.floor(Math.random() * width);
    const randomHeightValue = Math.floor(Math.random() * height);

    switch (posIdx) {
      case 0:
        return (position = {
          x: randomWidthValue,
          y: 0,
        });
      case 1:
        return (position = {
          x: width,
          y: randomHeightValue,
        });
      case 2:
        return (position = {
          x: randomWidthValue,
          y: height,
        });
      case 3:
        return (position = {
          x: 0,
          y: randomHeightValue,
        });
      default:
        return (position = {
          x: randomWidthValue,
          y: 0,
        });
    }
  };

  const shuffle = (array) => {
    for (let index = array.length - 1; index > 0; index--) {
      const randomPosition = Math.floor(Math.random() * (index + 1));

      const temporary = array[index];
      array[index] = array[randomPosition];
      array[randomPosition] = temporary;
    }
  };

  const createImg = (p5, color) => {
    return p5.createImg(`./p5-image/burst_${color}.gif`);
  };

  ///// p5.js sketch /////
  const sketch = (p5) => {
    p5.preload = () => {
      shuffle(shapes);
      fontStyle = p5.loadFont("./fonts/jalnan/jalnanOTF.otf");
      characters = shapes.map(({ shape, color }) => {
        return {
          img: p5.loadImage(`./p5-image/${shape}.png`),
          color,
        };
      });
    };

    p5.setup = () => {
      p5.createCanvas(window.innerWidth, window.innerHeight - 100);

      p5.imageMode(p5.CENTER);

      characters = setCharactersWithNames();
      shuffle(characters);
      const size = setSizeCharacter(p5);

      movers = characters.map((character, idx) => {
        const position = setStartPosition(idx, p5);
        return new Mover({
          id: idx,
          img: character.img,
          burstImg: character.burstImg,
          color: character.color,
          name: character.name,
          team: character.team,
          startX: position.x,
          startY: position.y,
          font: fontStyle,
          size,
          p5,
        });
      });

      leftMovers = movers.slice();
    };

    p5.draw = () => {
      p5.background(255);

      movers.forEach((mover) => {
        mover.update();

        mover.display();
        mover.checkEdges();

        movers.forEach((cMover) => {
          if (mover !== cMover) {
            mover.checkCollision(cMover);
          }

          if (cMover.colCount > 5) {
            cMover.isDone = true;

            bursters.push(
              new Burster({
                burstImg: createImg(p5, cMover.color),
                size: cMover.sizeW,
                p5,
              })
            );

            bursters.forEach((burster) => {
              burster.display(cMover.pos.x, cMover.pos.y);
              setTimeout(() => {
                burster.hide();
              }, 200);
            });

            const index = leftMovers.indexOf(cMover);
            leftMovers.splice(index, 1);
            movers = leftMovers.slice();
          }

          if (leftMovers.length === 1) {
            cMover.setWinnerMove();
            return;
          }
        });
      });
    };
  };

  useEffect(() => {
    setNameArr(setNamesInArray(applicants));
  }, [applicants]);

  useEffect(() => {
    replay && window.location.reload();
  }, [replay]);

  return nameArr.length && <ReactP5Wrapper sketch={sketch} />;
};

export default Lottery;
