import { ReactComponent as CloseIcon } from "../../static/images/svgs/close.svg";
import { ItemContainer, Wrapper } from "./itemsContainer.styles";

const ItemsContainer = ({ items, deleteItem }) => {
  const modifyItem = (item) => {
    const engName = item?.split(" ")[1];
    return engName;
  };
  return (
    <Wrapper>
      <ItemContainer>
        {items.map((item, idx) => {
          return (
            <li key={idx} className="selected-item">
              <p>
                {item.team || item.dept || modifyItem(item.name)}
                <button onClick={() => deleteItem(item)}>
                  <CloseIcon width={"16px"} height={"16px"} />
                </button>
              </p>
            </li>
          );
        })}
      </ItemContainer>
    </Wrapper>
  );
};

export default ItemsContainer;
