import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import TrieSearch from "trie-search/src/TrieSearch";
import peopleData from "data/company";
import DropDown from "components/dropDown/DropDown";
import Counter from "components/counter/Counter";
import SearchAddBar from "components/searchAddBar/SearchAddBar";
import ItemsContainer from "components/itemsContainer/ItemsContainer";
import Background from "components/background/Background";
import Button from "components/button/Button";
import Logo from "components/logoContainer/Logo";
import SetPeoplePage from "./SetPeople.styles.js";

const SetPeople = () => {
  const navigate = useNavigate();
  const [input, setInput] = useState("");
  const [searchedItems, setSearchedItems] = useState([]);
  const [selectedItems, setSelectedItems] = useState([]);
  const [peopleCount, setPeopleCount] = useState(0);
  const [countInput, setCountInput] = useState(0);
  const counterWrapperRef = useRef(null);

  const trie = useMemo(() => new TrieSearch(["dept", "team", "name"]), []);
  trie.addAll(peopleData);

  const classifyItems = useCallback((searchedItems) => {
    const peopleItems = [];
    const deptItems = [];
    const teamItems = [];

    searchedItems.forEach((item) => {
      if (item.name) {
        peopleItems.push(item);
      }
      if (item.dept) {
        deptItems.push(item);
      }
      if (item.team) {
        teamItems.push(item);
      }
    });

    return { peopleItems, deptItems, teamItems };
  }, []);

  const triSearchInput = useCallback(
    (str) => {
      const searchedItems = trie.search(str);
      return classifyItems(searchedItems);
    },
    [classifyItems, trie]
  );

  const sortPeopleItems = (peopleItems, input) => {
    if (peopleItems.length < 2 || !input) return peopleItems;
    const sortedItems = [...peopleItems];

    sortedItems.sort((a, b) => {
      if (!a.name || !b.name) return 0;

      const koEngNameA = a.name?.split(" ")[1];
      const koEngNameB = b.name?.split(" ")[1];

      if (koEngNameA.startsWith(input) && !koEngNameB.startsWith(input)) {
        return -1;
      }
      if (koEngNameB.startsWith(input) && !koEngNameA.startsWith(input)) {
        return 1;
      }
      return 0;
    });

    return sortedItems;
  };

  const organizedSearchedItems = useCallback((searchedItemsObj, input) => {
    const { peopleItems, deptItems, teamItems } = searchedItemsObj;
    const sortedPeopleItems = sortPeopleItems(peopleItems, input);
    return [...deptItems, ...teamItems, ...sortedPeopleItems];
  }, []);

  const countPeople = useCallback((selectedItems) => {
    let count = 0;

    selectedItems.forEach((item) => {
      if (item.name) {
        count++;
        return;
      }
      if (item.team) {
        if (item.captain.name !== "") {
          count += 1;
        }
        count += item.member.length;
        return;
      }
      if (item.dept) {
        item.captain.name !== "" && count++;
        item.teams.forEach((team) => {
          if (team.captain.name !== "") {
            count++;
          }
          count += team.member.length;
        });

        return;
      }
    });

    setPeopleCount(() => count);
  }, []);

  const addClass = () => {
    counterWrapperRef.current.classList.add("mb10");
  };

  const removeClass = () => {
    counterWrapperRef.current.classList.remove("mb10");
  };

  useEffect(() => {
    const searchedItemsObj = triSearchInput(input);
    const sortedItems = organizedSearchedItems(searchedItemsObj, input);
    if (!sortedItems) return;
    setSearchedItems(() => sortedItems);
  }, [input, triSearchInput, organizedSearchedItems]);

  useEffect(() => {
    countPeople(selectedItems);
    selectedItems.length > 0 ? removeClass() : addClass();
  }, [selectedItems, countPeople]);

  const selectItem = (item) => {
    if (selectedItems.includes(item)) return;
    setSelectedItems((prevItem) => [...prevItem, item]);
    setInput(() => "");
  };

  const deleteItem = (item) => {
    const newSelectedItems = selectedItems.filter(
      (sItem) => sItem.name !== item.name
    );
    setSelectedItems(() => newSelectedItems);
  };

  const setNewItem = (inputItem) => {
    if (inputItem === "") return;
    setSelectedItems((prevItems) => [
      ...prevItems,
      { name: `engName ${inputItem} koName` },
    ]);
  };

  const checkValidation = () => {
    //TODO: alert창 만들기
    if (countInput !== "" && Number(countInput) < 2) {
      console.log("countInput 이 있는데 2개 이하면 alert");
      return false;
    }
    if (
      countInput === "" &&
      selectedItems.length < 2 &&
      selectedItems[0].belong
    ) {
      console.log(
        "countInput 이 없고, 선택아이템이 1개 이하면서 team이나, member가 없으면 alert"
      );
      return false;
    }
    return true;
  };

  const goToLotteryPage = () => {
    if (!checkValidation()) return;
    navigate("/lottery", {
      state: { selectedItems, countInput: Number(countInput) },
    });
  };

  return (
    <SetPeoplePage>
      <Background />
      <Logo />
      <section className="set-people-container">
        <section className="counter-section mb10" ref={counterWrapperRef}>
          <Counter
            count={peopleCount}
            onInput={setCountInput}
            inputValue={countInput}
            disabled={selectedItems.length !== 0}
          />
          {selectedItems.length ? (
            <ItemsContainer items={selectedItems} deleteItem={deleteItem} />
          ) : null}
        </section>
        <section className="search-section">
          <SearchAddBar
            onChangeInput={setInput}
            onClickAdd={setNewItem}
            addInput={input}
          />
          {searchedItems.length ? (
            <DropDown items={searchedItems} onClick={selectItem} />
          ) : null}
        </section>
        <section className="start-button">
          <Button
            onClick={goToLotteryPage}
            disabled={peopleCount === 0 && countInput === ""}
          >
            START
          </Button>
        </section>
      </section>
    </SetPeoplePage>
  );
};

export default SetPeople;
