import { ReactComponent as PlusIcon } from "../../static/images/svgs/plus.svg";
import InputContainer from "./SearchAddBar.styles";

const SearchAddBar = ({ onChangeInput, onClickAdd, addInput }) => {
  return (
    <InputContainer>
      <input
        type="text"
        placeholder="search or add name..."
        onChange={(e) => {
          onChangeInput(e.target.value);
        }}
        value={addInput}
        maxLength={10}
      />
      <button onClick={() => onClickAdd(addInput)}>
        <PlusIcon width={"24px"} height={"24px"} />
      </button>
    </InputContainer>
  );
};

export default SearchAddBar;
